* {
    font-family: Georgia, 'Times New Roman', Times, serif;
    
}

.gridArea {
    margin-top: 30px;
    table {
        max-width: 700px;
        margin: auto;
        user-select: none;
        td{
            width: 20px;
            height: 20px;
            &.selected{
                color: rgb(238, 13, 69);
            }
        }
    }
}

.word-list{
    &.found{
     text-decoration: line-through;
    }
}